import { HomePage, GenericPage, ProfilePage } from "./pages";
import { withNavigationWatcher } from "./contexts/navigation";

export const generics = [
  { name: "ventas", icon: "bulletlist", isAdmin: false },
  {
    name: "stocks_clientes",
    subMenuOf: "Stocks",
    icon: "none",
    isAdmin: false,
  },
  { name: "stocks_locales", subMenuOf: "Stocks", icon: "none", isAdmin: false },
  {
    name: "movimientos_stock",
    subMenuOf: "Stocks",
    icon: "none",
    isAdmin: false,
  },
  { name: "productos", icon: "product", isAdmin: true },
  { name: "clientes", icon: "tags", isAdmin: false },
  {
    name: "medio_pagos",
    icon: "bulletlist",
    title: "Medios de pago",
    isAdmin: false,
  },
  { name: "locales", icon: "edit", title: "Puntos de venta", isAdmin: true },
  { name: "usuarios", icon: "folder", isAdmin: true },
];

const routes = [
  {
    path: "/home",
    element: HomePage,
    isAdmin: false,
  },
  {
    path: "/profile",
    element: ProfilePage,
    isAdmin: true,
  },
];

generics.map((view) => {
  routes.push({
    path: "/" + view.name,
    element: GenericPage,
  });
});

export default routes.map((route) => {
  return {
    ...route,
    element: withNavigationWatcher(route.element, route.path),
  };
});
