import { generics } from "./app-routes";
import { getUserRole } from "./api/auth";
let onlyAdmin = getUserRole() === 1;

let navigation = [
  {
    text: "Home",
    path: "/home",
    icon: "home",
  },
];

// no generic
navigation.push({
  text: "Stocks",
  icon: "folder",
  items: [
    {
      text: "Stock Clientes",
      path: "/stocks_clientes",
    },
    {
      text: "Stock Puntos de venta",
      path: "/stocks_locales",
      visible: onlyAdmin,
    },
    {
      text: "Movimientos stocks",
      path: "/movimientos_stock",
      visible: onlyAdmin,
    },
  ],
});

generics.map((item) => {
  !item.subMenuOf &&
    navigation.push({
      text: item.title || item.name.replace("_", " "),
      path: "/" + item.name,
      icon: item.icon !== "none" ? item.icon : "folder",
      visible: item.isAdmin ? onlyAdmin : true,
    });
});

export { navigation };
