import axios from "axios";
import { baseURL } from "../../package.json";
import dayjs from "dayjs";

async function getAll(table) {
  return axios
    .get("/offline/" + table) // "/offline/" +
    .then((response) => {
      return response.data.Error ? data[table] : trimData(response.data);
    })
    .catch((error) => {
      return data[table];
    });
}

async function getById(table, id) {
  return axios
    .get(baseURL + table + "/" + id)
    .then((response) => (response.data.Error ? data[table] : response.data))
    .catch((error) => console.log(error.response));
}

async function adding(table, newElement) {
  newElement = trimData(newElement, true);
  let params = formatParams(newElement);
  return axios
    .post(baseURL + table + params)
    .then((response) =>
      response.data.Error ? response.data.Error : response.data
    )
    .catch((error) => console.log(error.response));
}

async function updating(table, newElement) {
  let { id } = newElement;
  let params = formatParams(newElement);
  return axios
    .patch(baseURL + table + "/" + id + params)
    .then((response) => (response.data.Error ? data[table] : response.data))
    .catch((error) => console.log(error.response));
}

async function deleting(table, id) {
  return axios
    .delete(baseURL + table + "/" + id)
    .then((response) => (response.data.Error ? data[table] : response.data))
    .catch((error) => console.log(error.response));
}

function formatParams(params) {
  let res = "?";
  Object.keys(params).map((key) => {
    let value = isNumber(key) && !params[key] ? 0 : params[key];
    res += res === "?" ? "" : "&";
    res += key + "=" + value;
  });
  return res;
}

function isNumber(field) {
  return [
    "tel",
    "precios",
    "role",
    "precio_particular",
    "precio_total",
  ].includes(field.toLowerCase());
}

function trimData(data, isObject = false) {
  if (!isObject) {
    // isArray is GettingAll
    data.forEach((x) =>
      Object.keys(x).forEach((y) => {
        try {
          // if (y === "fecha") x[y] = dayjs(x[y]).format("DD-MM-YYYY h:mm");
          x[y] = x[y].trim();
        } catch (err) {}
      })
    );
    return data;
  } else {
    // isObject is Adding
    Object.keys(data).map((e) => {
      if (e === "fecha") data[e] = dayjs(new Date()).format("DD-MM-YYYY h:mm");
      if (e === "usuario_id") data[e] = 1; // getUserId()
      if (e === "precio_total") data[e] = 259; // getUserId()
    });
    return data;
  }
}

export { getAll, getById, adding, updating, deleting, formatParams };

