import React, { useEffect, useState } from "react";
import "./home.scss";
import {
  Chart,
  Series,
  CommonSeriesSettings,
  Label,
  Format,
  Legend,
  Export,
} from "devextreme-react/chart";
import { getAll } from "../../api/services";

const grossProductData = [
  {
    producto: "20L Retornable",
    MC3: 153,
    B_Jardin: 125,
  },
  {
    producto: "12L Retornable",
    MC3: 110,
    B_Jardin: 90,
  },
  {
    producto: "12L Descartable",
    MC3: 43,
    B_Jardin: 35,
  },
];

// https://js.devexpress.com/Demos/Widgetsgallery/

export default function Home() {
  const [DS, setDS] = useState([]);

  useEffect(() => {
    getAll("stocks_locales").then((response) => {
      if (!response?.length) return;
      let withOptions = Object.keys(response[0])?.filter((col) => {
        return col?.includes("_id");
      });
      let cant = withOptions.length;
      let count = 0;
      if (cant) {
        withOptions?.map((col) => {
          let table_name = col.replace("_id", "s");
          let field = col.replace("_id", "");
          getAll(table_name).then((newDS) => {
            if (!newDS?.length) return;
            newDS?.forEach((p) => (p.nombre = p.nombre?.trim()));
            response.map((x) => {
              let temp = newDS?.find((y) => y.id === x[col])?.nombre?.trim();
              if (temp) x[field] = temp;
            });
            count++;
            if (count === cant) {
              console.log("1 DS ", response);
              setDS(response);
            }
          });
        });
      } else {
        setDS(response);
      }
    });
  }, []);
  console.log(" DS ", DS);

  return (
    <React.Fragment>
      <h2 className={"content-block"}>Datos generales</h2>
      <div className={"content-block"}>
        <div className={"dx-card responsive-paddings"}>
          <Chart
            id="chart"
            // title="20L"
            dataSource={grossProductData} // DS
            onPointClick={(e) => e.target.select()}
          >
            <CommonSeriesSettings
              argumentField="producto"
              type="bar"
              hoverMode="allArgumentPoints"
              selectionMode="allArgumentPoints"
            >
              <Label visible={true}>
                <Format type="fixedPoint" precision={0} />
              </Label>
            </CommonSeriesSettings>
            {/* <Series valueField="producto" name="producto" /> */}
            <Series valueField="MC3" name="Mirador 3" />
            <Series valueField="B_Jardin" name="Barrio Jardin" />
            <Legend
              verticalAlignment="bottom"
              horizontalAlignment="center"
            ></Legend>
            <Export enabled={true} />
          </Chart>
        </div>
      </div>
    </React.Fragment>
  );
}
